import { request } from '../../request.js'
export default {
	data() {
		return {
			// 用户命
			username: '',
			// 密码
			code: '',
			// 手机号
			cellphone: '',
			// 手机验证码
			vertification: '',
			// 控制按钮是否可以点击
			disabled: false,
			get: true,
			down: false,
			again: false,
			// 倒计时秒数
			count: 60
		};
	},
	methods: {
		// 跳转到登录页
		login() {
			uni.redirectTo({
				url: '../login/login'
			})
		},
		// 检测用户名
		user() {
			console.log('user');
			let test = /[\u4E00-\u9FA5\uF900-\uFA2D]/.test(this.username);
			if(test || this.username == '') {
				uni.showToast({
					title: '请输入合法用户名',
					icon: 'none'
				})
				return
			}
		},
		// 检测密码
		password() {
			console.log('password');
			let test = /[\u4E00-\u9FA5\uF900-\uFA2D]/.test(this.code);
			if(test || this.code == '') {
				uni.showToast({
					title: '请输入合法密码',
					icon: 'none'
				})
			}
		},
		// 检测手机号码是否注册过
		async phone() {
			if(this.cellphone == '' || !(/^1[3456789]\d{9}$/.test(this.cellphone))) {
				uni.showToast({
					title: '请输入合法的手机号',
					icon: 'none'
				})
				return false
			}
			await request({
				url: 'index.php?s=/wap/login/checkMobileIsHas',
				method: 'post',
				data: {
					mobile: this.cellphone
				}
			}).then(res => {
				if(res.data) {
					uni.showToast({
						title: '手机号已被注册',
						icon:'none'
					});
					
				}
			})
		},
		// 检测验证码
		QRcode() {
			if(this.vertification == '') {
				uni.navigateTo({
					title: '请输入合法验证码',
					icon: 'none'
				})
			}
		},
		// 发送验证码
		getCode() {	
			if(this.cellphone != '' && (/^1[3456789]\d{9}$/.test(this.cellphone))) {
				uni.showToast({
					title: '短信发送成功',
					icon: 'none'
				});
				this.disabled = true;
				this.get = false;
				this.down = true;
				this.again = false
				let timer = setInterval(() => {
					this.count = this.count-1;
					if(this.count <= 0) {
						this.disabled = false;
						this.down = false;
						this.again = true;
						this.count = 5;
						clearInterval(timer)
					}
				},1000)
					request({
						url: '/index.php?s=/wap/Login/sendSmsRegisterCode',
						method: 'post',
						data: {
							vertification: 123,
							mobile: this.cellphone,
							vevry:this.vevryfly
						}
					}).then(res => {
						
					})
			}
			else {
				uni.showToast({
					title: '请输入合法手机号',
					icon: 'none'
				})
			}
		},
		// 跳转到重置密码页
		reset() {
			request({
				url:"/index.php?s=/wap/Login/wchatBindMobile",
				method:"POST",
				data: {
					vertification: this.vertification,
					mobile: this.cellphone
				}
			}).then(res=>{
				if(res.data.code >0){
					uni.showToast({
						title:"绑定成功!",
						icon:"none"
					})
					setTimeout(function(){
						uni.switchTab({
							url:"/pages/mine/mine"
						});
					},2000)
				}else{
					uni.showToast({
						title:res.data.msg,
						icon:"none",
					})
				}
			})
		}
	}
}
